.res-item {
  display: flex;
  
 
  align-content: center;
  text-align: left;
  justify-content: center;
  width: 100%;
}

.ul {
  list-style: none;
}

li {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  padding: 5px;
  margin-bottom: 5px;
  box-shadow: 1px 1px 1px black;

}

span {
  display: flex;

  
}







